y: none;\n}\n#startVAD.on .fa-stop[data-v-3a451344]{\n  display: initial;\n}\n#startVAD.on.loading .fa-spinner[data-v-3a451344]{\n  display: initial;\n}\naudio[data-v-3a451344]{\n  outline: none;\n  width: 100%;\n  padding-bottom: 15px;\n}\n.audio[data-v-3a451344]{\n  padding: 0px 15px 0px 15px;\n}\ndiv.confidence[data-v-3a451344]{\n  display: -webkit-inline-box;\n  display: -ms-inline-flexbox;\n  display: inline-flex;\n  -ms-flex-wrap: wrap;\n      flex-wrap: wrap;\n}\ndiv.char[data-v-3a451344]{\n  /*line-height: 20px;*/\n}\ndiv.char-wrapper[data-v-3a451344]{\n  width: 20px;\n  display: inline-block;\n  text-align: center;\n}\ndiv.word[data-v-3a451344]{\n  margin-right: 20px;\n}\ndiv.word[data-v-3a451344]:last-child{\n  margin-right: 0px;\n}\ndiv.word-wrapper[data-v-3a451344]{\n  /*width: 20px*/\n}\nspan.char[data-v-3a451344]{\n  display: inline-block;\n  width: 20px;\n  text-align: center;\n}\nspan.prob[data-v-3a451344]{\n  display: inline-block;\n  font-size: 8px;\n  /*line-he*/ight: 0px;\n}\n", "", {"version":3,"sources":["/Users/livestream/tehikudev/korero-maori-demos/vad-korero-demo/src/components/src/components/Korero.vue"],"names":[],"mappings":";AAmPA;EACA,oDAAA;EACA,oCAAA;EACA,mCAAA;EACA,mBAAA;EACA,eAAA;EACA,gBAAA;CACA;AACA;EACA,mBAAA;EACA,WAAA;EACA,SAAA;EACA,eAAA;EACA,YAAA;EACA,YAAA;EACA,iBAAA;EACA,eAAA;EACA,gBAAA;EACA,cAAA;EACA,iBAAA;CACA;AACA;EACA,mBAAA;EACA,YAAA;EACA,4BAAA;EACA,iBAAA;EACA,sBAAA;EACA,iBAAA;EACA,mBAAA;EACA,gDAAA;EACA,wBAAA;CACA;AACA;EACA,YAAA;CACA;AACA;EACA,mBAAA;CACA;AACA;EACA,iBAAA;CACA;AAEA;EACA,cAAA;CACA;AACA;EACA,qBAAA;EAAA,qBAAA;EAAA,cAAA;EACA,6BAAA;EAAA,8BAAA;MAAA,2BAAA;UAAA,uBAAA;EACA,yBAAA;MAAA,sBAAA;UAAA,wBAAA;CACA;AACA;EACA,cAAA;EACA,mBAAA;CACA;AACA;EACA,qBAAA;EACA,WAAA;EACA,wBAAA;EACA,mBAAA;EACA,oBAAA;CACA;AACA;EACA,WAAA;EACA,qBAAA;EAAA,qBAAA;EAAA,cAAA;EACA,6BAAA;EAAA,8BAAA;MAAA,2BAAA;UAAA,uBAAA;EACA,mBAAA;EACA,sBAAA;MAAA,kBAAA;EACA,kCAAA;EACA,6CAAA;EACA,0BAAA;MAAA,uBAAA;UAAA,oBAAA;EACA,kBAAA;EACA,cAAA;EACA,mBAAA;EACA,oBAAA;CACA;AAEA;EACA,qBAAA;EAAA,qBAAA;EAAA,cAAA;EACA,YAAA;EACA,iBAAA;EACA,cAAA;EACA,4BAAA;MAAA,mBAAA;EACA,0BAAA;MAAA,uBAAA;UAAA,oBAAA;CACA;AACA;EACA,WAAA;EACA,eAAA;EACA,aAAA;EACA,YAAA;EACA,uBAAA;CACA;AACA;EACA,aAAA;EACA,YAAA;EACA,wBAAA;EACA,mBAAA;CACA;AACA;EACA,kBAAA;CACA;AACA;EACA,aAAA;EACA,iBAAA;CACA;AACA;EACA,eAAA;EACA,aAAA;CACA;AACA;EACA,aAAA;EACA,iBAAA;EACA,wBAAA;CACA;AACA;EACA,sBAAA;CACA;AACA;EACA,WAAA;EACA,4BAAA;MAAA,mBAAA;EACA,aAAA;EACA,cAAA;EACA,gCAAA;EACA,aAAA;EACA,YAAA;EACA,aAAA;EACA,wBAAA;EACA,oBAAA;EACA,kBAAA;EACA,gBAAA;CACA;AACA;EACA,2BAAA;CACA;AACA;EACA,0CAAA;EACA,+BAAA;CACA;AACA;EACA,uCAAA;CACA;AACA;EACA,cAAA;CACA;AACA;EACA,cAAA;CACA;AACA;EACA,iBAAA;CACA;AACA;EACA,iBAAA;CACA;AACA;EACA,cAAA;EACA,YAAA;EACA,qBAAA;CACA;AACA;EACA,2BAAA;CACA;AAEA;EACA,4BAAA;EAAA,4BAAA;EAAA,qBAAA;EACA,oBAAA;MAAA,gBAAA;CACA;AACA;EACA,sBAAA;CACA;AACA;EACA,YAAA;EACA,sBAAA;EACA,mBAAA;CACA;AACA;EACA,mBAAA;CACA;AACA;EACA,kBAAA;CACA;AACA;EACA,eAAA;CACA;AACA;EACA,sBAAA;EACA,YAAA;EACA,mBAAA;CACA;AACA;EACA,sBAAA;EACA,eAAA;EACA,WAAA,UAAA;CACA","file":"Korero.vue","sourcesContent":["<template>\n  <div class=\"Korero\">\n    <div class=\"header\">\n      <button id=\"startVAD\" v-bind:class=\"{ vad: vadOn, on: recordingOn, 'loading': loadRecording}\">\n        <i class=\"fa fa-microphone\" ></i>\n        <i class=\"fa fa-stop\" ></i>\n        <i class=\"fa fa-spinner fa-spin\"></i>\n      </button>\n      <div id=\"vadStatus\" v-bind:class=\"{ active: vadOn, 'mobile': isMobile()}\">\n        <div>\n          <canvas id=\"canvas\" v-if=\"!isMobile()\"></canvas>\n          <span v-if=\"isMobile() && vadOn && recordingOn\">Voice Detected</span>\n          <span v-if=\"isMobile() && recordingOn && !vadOn\">Listening</span>\n          <span v-if=\"isMobile()\">Hit Record</span>\n        </div>\n      </div>\n    </div>\n\n    <div class=\"body\">\n      <div id=\"transcriptions\">\n        <div v-for=\"(item, index) in transcriptions\" class='transcription' v-bind:class=\"[item.status]\" v-if=\"item.status != 'Failed'\"> \n          <button class=\"delete\" v-if=\"item.status != 'Transcribing'\" v-on:click=\"deleteObject(index)\"><i class=\"fa fa-times\"></i></button>\n          <div class='text'>\n            <i class=\"fa fa-spinner fa-spin\" v-bind:class=\"[item.status]\" v-if=\"item.status == 'Transcribing'\" ></i>\n            <div v-if=\"item.show_metadata\">\n              <div class='confidence'>\n                <div class=\"char\" v-for=\"character in item.metadata\">\n                  <div class=\"char-wrapper\" v-if=\"character.char==' '\"\n                       :style=\"{backgroundColor: `rgb(255, 40, 40, ${1-character.prob})`}\">\n                       <span class=\"char\">&nbsp;&nbsp;</span>\n                       <br><span class=\"prob\">&nbsp;{{character.prob.toFixed(2)}}&nbsp;</span>\n                  </div>\n                  <div class=\"char-wrapper\" v-if=\"character.char!=' '\"\n                       :style=\"{\n                          backgroundColor: `rgba(255, 40, 40, ${1-character.prob})`\n                        }\">\n                      <span class=\"char\">{{character.char}}</span>\n                      <br><span class=\"prob\">&nbsp;{{character.prob.toFixed(2)}}&nbsp;</span>\n                  </div>\n                </div>\n              </div>\n              <div><br></div>\n              <div class='confidence'>          \n                <div class=\"char\" v-for=\"character in item.metadata\">\n                  <div class=\"char-wrapper\" v-if=\"character.acoustic==' '\"\n                       :style=\"{backgroundColor: `rgb(255, 40, 40, ${character.entropy})`}\">\n                       <span class=\"char\">&nbsp;&nbsp;</span>\n                       <br><span class=\"prob\">&nbsp;{{character.entropy.toFixed(2)}}&nbsp;</span>\n                  </div>\n                  <div class=\"char-wrapper\" v-if=\"character.acoustic!=' '\"\n                       :style=\"{\n                          backgroundColor: `rgba(255, 40, 40, ${character.entropy})`\n                        }\">\n                      <span class=\"char\">{{character.acoustic}}</span>\n                      <br><span class=\"prob\">&nbsp;{{character.entropy.toFixed(2)}}&nbsp;</span>\n                  </div>\n                </div>\n              </div>\n              <div><br></div>\n              <div class='confidence'>          \n                <div class=\"word\" v-for=\"word in item.words\"\n                  :style=\"{\n                      backgroundColor: `rgba(255, 40, 40, ${1-word.prob})`\n                    }\">\n                  <div class=\"word-wrapper\" \n                    >\n                      <span class=\"char\" v-for=\"char in word.word\" v-if=\"char != ' '\">{{char}}</span>\n                      \n                  </div><span class=\"prob\">{{word.prob.toFixed(2)}}</span>\n                </div>\n              </div>\n            </div>\n          </div>\n          <div v-if=\"!item.show_metadata\">\n            <div class='text' v-if=\"item.status != 'Transcribing'\">{{item.text}}</div>\n          </div>\n          <div class=\"audio\">\n            <audio v-if=\"item.audio_url\" :src=\"item.audio_url\" type=\"audio/mp3\" controls v-on:play=\"stopRecording\"></audio>\n          </div>\n\n        </div>\n      </div>\n    </div>\n  </div>\n</template>\n\n<script>\nimport recorder from '../lib/recorder.js'\nconst ApiAuth = require('../../api_auth')\nconst api_auth = ApiAuth.api_auth;\nconst axios = require('axios');\nexport default {\n  name: 'KÅrero',\n  data () {\n    return {\n      vadOn: false,\n      recordingOn: false,\n      recorder: null,\n      buttonText: 'Start',\n      transcriptions: [],\n      icon: 'fa-microphone',\n      loadRecording: false\n    }\n  },\n  methods: {\n    deleteObject: function (e) {\n      this.transcriptions.splice(e, 1)\n    },\n    stopRecording: function () {\n      try{this.recorder.stop();}\n      catch(e){}\n      this.recorder=null;\n      this.vadOn = false;\n      this.recordingOn = false\n    },   \n    isMobile() {\n      if(/Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Mobile|SamsungBrowser|Opera Mini/i.test(navigator.userAgent)) {\n        console.log(navigator.userAgent)\n        return true\n      } else {\n        return false\n      }\n    },\n    p_not_word: function(n, probabilities){\n      if (n == 0){\n        return 0\n      }                                                                \n      else {\n        return probabilities[n - 1] * this.p_not_word(n - 1, probabilities) + (1 - probabilities[n - 1])\n      }\n    },\n    p_word: function(probabilities){\n      return 1 - this.p_not_word(probabilities.length, probabilities)      \n    },\n\n  },\n  mounted: function () {\n\n    startVAD.onclick = event => {\n\n      if (this.recorder==null){\n        this.loadRecording = true\n        this.recorder = new recorder({\n          putRecording : (record) => {\n            var transcription = {\n              status: 'Transcribing',\n              text: null,\n              audio: null,\n            }\n            this.transcriptions.unshift(transcription)\n            var formData = new FormData();\n            formData.enctype=\"multipart/form-data\";\n            formData.append('audio_file', record.blob, 'audio_file.mp3')            \n            axios.post(\n              api_auth['url'],\n              formData,\n              {\n                headers: api_auth['headers'],\n              })\n            .then((response) => {\n              if (response.data.transcription == '' || response.data.transcription == ' ' ){\n                transcription.status = 'Failed'\n                return\n              }\n              transcription['text'] = response.data.transcription\n              transcription['show_metadata'] = false\n              if (response.data.metadata){\n                transcription['show_metadata'] = true\n                transcription['metadata'] = response.data.metadata\n\n                var words = []\n                var probs = []\n                var prob = []\n                var word = ''\n                var start = 0\n                for (var i=0; i <response.data.metadata.length; i++){\n                  word = word + response.data.metadata[i].char\n                  prob.push(response.data.metadata[i].prob)\n                  if (response.data.metadata[i].char != ' '){\n                    continue\n                  } else {\n                    words.push({'word': word, 'prob': this.p_word(prob)})\n                    prob = []\n                    start = i\n                    word = ''\n                  }\n                }\n                words.push({'word': word, 'prob': this.p_word(prob)})\n                console.log(words)\n                transcription['words'] = words\n                console.log(transcription['metadata'])\n              }\n              \n              transcription['audio_url'] = record.url\n              transcription['status'] = 'Success'\n            })\n            .catch((error) => {\n              transcription.status = 'Failed'\n            })\n\n          },\n          afterRecording  : (stream) =>{\n            this.buttonText = 'Start'            \n            this.recorder=null;\n          },\n          pauseRecording  : function(){console.log('paused')},\n          micFailed       : function(){console.log('failed')},\n          voiceStop: () => {\n            \n            this.vadOn = false\n          },\n          voiceStart: () => {\n            this.vadOn = true\n\n          },\n          canvasID: this.isMobile() ? null : 'canvas',\n          bitRate         : 64,\n          sampleRate      : 44100,\n          // format          : this.format\n        })\n      }\n\n      if (this.recordingOn){\n        this.recorder.stop();\n        this.recorder=null;\n        this.vadOn = false;\n        this.recordingOn = false\n        // this.buttonText = 'Start'\n      } else {\n        this.recorder.start()\n        this.recordingOn = true\n        this.buttonText = 'Stop'\n        this.loadRecording = false\n      }\n      \n\n      }\n  }\n}\n</script>\n\n<style scoped>\n\n.Korero {\n  font-family: 'Avenir', Helvetica, Arial, sans-serif;\n  -webkit-font-smoothing: antialiased;\n  -moz-osx-font-smoothing: grayscale;\n  text-align: center;\n  color: #2c3e50;\n  font-size: 14px;\n}\nbutton.delete{\n  position: absolute;\n  right: 0px;\n  top: 0px;\n  display: block;\n  border: 0px;\n  margin: 2px;\n  font-size: 0.7em;\n  color: #696969;\n  cursor: pointer;\n  outline: none;\n  background: none;\n}\n.transcription{\n  position: relative;\n  width: 100%;\n  margin: 20px 30px 20px 30px;\n  max-width: 500px;\n  /*margin-top: 20px; */\n  font-size: 1.5em;\n  border-radius: 8px;\n  /*box-shadow: 0px 2px 6px -2px rgba(0,0,0,1)  */\n  border: 1px solid black;\n}\n.transcription.Transcribing{\n  border: 0px;\n}\n.transcription .text {\n  padding: 15px 20px;\n}\n.transcription:nth-of-type(1){\n  margin-top: 40px;\n}\n\ndiv.transcription.Success div.text [class*=fa]{\n  display: none;\n}\n.Korero, .header, .body{\n  display: flex;\n  flex-direction: column;\n  justify-content: center;\n}\n.body{\n  height: 100vh;\n  margin-top: -225px;\n}\n.header{\n  padding-bottom: 15px;\n  z-index: 1;\n  background-color: white;\n  padding-left: 15px;\n  padding-right: 15px;  \n}\n#transcriptions{\n  z-index: 0;\n  display: flex;\n  flex-direction: column;\n  overflow-y: scroll;\n  flex-wrap: nowrap;\n  -webkit-overflow-scrolling: touch;\n  -ms-overflow-style: -ms-autohiding-scrollbar; \n  align-items: center;  \n  margin-top: 232px;\n  height: 100vh;    \n  padding-left: 15px;\n  padding-right: 15px;\n}\n\n#vadStatus{\n  display: flex;\n  width: 100%;\n  max-width: 500px;  \n  height: 100px;\n  align-self: center;\n  align-items: center;\n}\n#vadStatus canvas{\n  z-index: 1;\n  display: block;\n  height: 100%;\n  width: 100%;\n  background-color: #333;\n}\n#vadStatus div{\n  height: 100%;\n  width: 100%;\n  border: 4px solid black;\n  border-radius: 4px;\n}\n#vadStatus.active div{\n  border-color: red;\n}\n#vadStatus.mobile{\n  height: 28px;\n  max-width: 150px;  \n}\n#vadStatus.mobile div span{\n  display: block;\n  padding: 4px;\n}\n#vadStatus.mobile div{\n  color: white;\n  font-weight: 700;\n  background-color: black;\n}\n#vadStatus.mobile.active div{\n  background-color: red;\n}\n#startVAD {\n  z-index: 2;\n  align-self: center;\n  margin: 15px;\n  outline: none;\n  background-color: rgb(40,40,40);\n  color: white;\n  width: 80px;\n  height: 80px;\n  border: 2px solid black;\n  border-radius: 40px;\n  padding: 8px 16px;\n  cursor: pointer;\n}\n#startVAD [class*=fa]{\n  font-size: 34px !important;\n}\n#startVAD.on {\n  background-color: rgba(255, 40, 40, 0.75);\n  border-color: rgb(200, 40, 40);\n}\n#startVAD.on.vad {\n  background-color: rgba(255, 40, 40, 1);\n}\n#startVAD.on .fa-microphone{\n  display: none;\n}\n#startVAD .fa-stop, #startVAD .fa-spinner{\n  display: none;\n}\n#startVAD.on .fa-stop{\n  display: initial;\n}\n#startVAD.on.loading .fa-spinner{\n  display: initial;\n}\naudio{\n  outline: none;\n  width: 100%;\n  padding-bottom: 15px;\n}\n.audio{\n  padding: 0px 15px 0px 15px;\n}\n\ndiv.confidence{\n  display: inline-flex;\n  flex-wrap: wrap;\n}\ndiv.char{\n  /*line-height: 20px;*/\n}\ndiv.char-wrapper{\n  width: 20px;\n  display: inline-block;\n  text-align: center;\n}\ndiv.word{\n  margin-right: 20px;\n}\ndiv.word:last-child{\n  margin-right: 0px;\n}\ndiv.word-wrapper{\n  /*width: 20px*/\n}\nspan.char{\n  display: inline-block;\n  width: 20px;\n  text-align: center;\n}\nspan.prob{\n  display: inline-block;\n  font-size: 8px;\n  /*line-he*/ight: 0px;\n}\n</style>\n"],"sourceRoot":""}]);

